<template>
  <div class="account-detail--container">
    <div class="header--content">
        <img :src="userInfo.avatar" alt="" class="avatar">
        <span class="id">{{userInfo.id}}</span>
    </div>

    <!-- 结构和类名按这个写，都规划好了 -->
    <div class="table--container">
      <router-link
        v-for="(item, $index) in selectOptions"
        :to="item.to"
        :key="$index"
        class="table-cell--content">
        <section class="router">
          <img class="cell--icon" :src="item.img_url" alt="">
          <div class="right-part">{{item.title}}</div>
        </section>
      </router-link>
    </div>
    <div class="center-btn--area">
      <div class="global--button_primary radius" @click="logout()">退出登录</div>
    </div>
    <div class="takeplace"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userInfo:{
        'id':'共信平台',
        'avatar':'https://om536p71r.qnssl.com/%E5%A4%B4%E5%83%8F.png',
      },
      selectOptions:[
        {
          'title':'账户信息',
          'to':'Identify',
          'img_url':'https://om536p71r.qnssl.com/%E8%B4%A6%E6%88%B7%E4%BF%A1%E6%81%AF.png',
        },
        {
          'title':'借款记录',
          'to':'MyBorrow',
          'img_url':'https://om536p71r.qnssl.com/%E5%80%9F%E6%AC%BE%20%284%29.png',
        },
        {
          'title':'我要借款',
          'to':'Borrow',
          'img_url':'https://om536p71r.qnssl.com/%E5%80%9F%E6%AC%BE%20%283%29.png',
        },
        {
          'title':'我要还款',
          'to':'Repay',
          'img_url':'https://om536p71r.qnssl.com/%E8%BF%98%E6%AC%BE%20%281%29.png',
        },
        {
          'title':'安全中心',
          'to':'',
          'img_url':'https://om536p71r.qnssl.com/%E5%AE%89%E5%85%A8%E4%B8%AD%E5%BF%83.png',
        },
        {
          'title':'系统消息',
          'to':'/MessageList',
          'img_url':'https://om536p71r.qnssl.com/%E7%B3%BB%E7%BB%9F_%E6%B6%88%E6%81%AF.png',
        },
      ]
    };
  },
  methods: {
    logout(){
      this.axios.get('user/logout')
          .then((res) => {
            console.log(res);
            window.localStorage.checkLogin = false;
            this.$router.replace({
              name: 'Home',
            });
          });
    }
  }
};
</script>



