<template>
  <div class="register--container">
    <div class="container--title">
      <img class="title--img" src="https://om536p71r.qnssl.com/%E9%93%B6%E8%A1%8C%20%281%29.png">
      <div class="title--text">共信金融</div>
    </div>
    <div class="input-group--container">
      <div class="input--container">
        <input class="input--el" placeholder="您的称呼" type="text" v-model="contactName">
        <span class="input--icon"><i class="iconfont">&#xe668;</i></span>
      </div>
      <div class="input--container">
        <input class="input--el" placeholder="11位手机号码" type="text" v-model="phoneNum">
        <span class="input--icon"><i class="iconfont">&#xe6d7;</i></span>
      </div>
    </div>

    <div class="input-group--container"> 
      <div class="input--long---container">
        <textarea v-model="feedbackText" class="input--longtext" placeholder="请在意见内容留下您的联系方式以便于我们的反馈！谢谢！" ></textarea>
      </div>
    </div>

    <div class="center-btn">
      <div @click="submitFeedbackHandler" class="global--button_primary radius">提交</div>
    </div>

    <div class="takeplace" style="height: 3rem;"></div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        phoneNum: '',
        contactName: '',
        feedbackText: '',
      };
    },
    mounted() {
    },
    methods: {
      submitFeedbackHandler() {
        const feedbackData = {
          phoneNum: this.phoneNum,
          contactName: this.contactName,
          feedbackText: this.feedbackText,
        };
        console.log(feedbackData);
        this.axios.post('/Template/updateContentTpl', feedbackData)
            .then((res) => {
              console.log(res.data);
            });
      },
    },
  };
</script>



