import Vue from 'vue';
import Router from 'vue-router';
import HelloWorld from '@/components/HelloWorld';
import Borrow from '@/components/Borrow';
import MyBorrow from '@/components/MyBorrow';
import Repay from '@/components/Repay';
import MyAccount from '@/components/MyAccount';
import More from '@/components/More';
import Home from '@/components/Home';
import Register from '@/components/Register';
import UserLogin from '@/components/UserLogin';
import AgentLogin from '@/components/AgentLogin';
import QandA from '@/components/QandA';
import FeedBack from '@/components/FeedBack';
import BorrowList from '@/components/BorrowList';
import ForgetPassword from '@/components/ForgetPassword';
import MyAccountInfo from '@/components/MyAccountInfo';
import MessageList from '@/components/MessageList';
import SelectIdentity from '@/components/SelectIdentity';
import Identify from '@/components/Identify';
import ActivityCenter from '@/components/ActivityCenter';
import BorrowWorkFlow from '@/components/BorrowWorkFlow';
import DelayWorkFlow from '@/components/DelayWorkFlow';
import FequentQuestion from '@/components/FequentQuestion';
import RepayWorkFlow from '@/components/RepayWorkFlow';
import TemsAndConditions from '@/components/TemsAndConditions';
import axios from 'axios';

Vue.use(Router);

const routes = [
  {
    path: '/Borrow',
    name: 'Borrow',
    component: Borrow,
    beforeEnter: (to, from, next) => {
      console.log('ddddddddddddddddddddd');
      axios.get('http://www.gzfinance.top/user/checkLogin')
      // axios.get('http://localhost/user/checkLogin')
          .then((res) => {
              if(res.data.code === 1){
                window.localStorage.checkLogin === 'true';
                next();
              } else {
                window.localStorage.checkLogin === 'false';
                router.replace({
                  name: 'Home',
                })
              }
          });
    }
  },
  {
    path: '/MyBorrow',
    name: 'MyBorrow',
    component: MyBorrow,
  },
  {
    path: '/Repay',
    name: 'Repay',
    component: Repay,
  },
  {
    path: '/MyAccount',
    name: 'MyAccount',
    component: MyAccount,
  },
  {
    path: '/More',
    name: 'More',
    component: More,
  },
  {
    path: '/Home',
    name: 'Home',
    component: Home,
    meta: {
      needLogin: false,
    },
  },
  {
    path: '/Register',
    name: 'Register',
    component: Register,
    meta: {
      needLogin: false,
    },
  },
  {
    path: '/UserLogin',
    name: 'UserLogin',
    component: UserLogin,
    meta: {
      needLogin: false,
    },
  },
  {
    path: '/AgentLogin',
    name: 'AgentLogin',
    component: AgentLogin,
    meta: {
      needLogin: false,
    },
  },
  {
    path: '/Identify',
    name: 'Identify',
    component: Identify,
  },
  {
    path: '/QandA',
    name: 'QandA',
    component: QandA,
  },
  {
    path: '/HelloWorld',
    name: 'HelloWorld',
    component: HelloWorld,
  },
  {
    path: '/ActivityCenter',
    name: 'ActivityCenter',
    component: ActivityCenter,
  },
  {
    path: '/BorrowWorkFlow',
    name: 'BorrowWorkFlow',
    component: BorrowWorkFlow,
  },
  {
    path: '/DelayWorkFlow',
    name: 'DelayWorkFlow',
    component: DelayWorkFlow,
  },
  {
    path: '/FequentQuestion',
    name: 'FequentQuestion',
    component: FequentQuestion,
  },
  {
    path: '/RepayWorkFlow',
    name: 'RepayWorkFlow',
    component: RepayWorkFlow,
  },
  {
    path: '/TemsAndConditions',
    name: 'TemsAndConditions',
    component: TemsAndConditions,
    meta: {
      needLogin: false,
    }
  },
  {
    path: '/FeedBack',
    name: 'FeedBack',
    component: FeedBack,
  },
  {
    path: '/BorrowList/:status',
    name: 'BorrowList',
    component: BorrowList,
  },
  {
    path: '/ForgetPassword',
    name: 'ForgetPassword',
    component: ForgetPassword,
    meta: {
      needLogin: false,
    },
  },
  {
    path: '/MyAccountInfo',
    name: 'MyAccountInfo',
    component: MyAccountInfo,
  },
  {
    path: '/MessageList',
    name: 'MessageList',
    component: MessageList,
  },
  {
    path: '/SelectIdentity',
    name: 'SelectIdentity',
    component: SelectIdentity,
  },
  {
    path: '/*',
    name: '404',
    component: Borrow,
  },
];

const router = new Router({
  routes,
});

router.beforeEach((to, from, next) => {
    if (to.meta.needLogin === false) {
      next();
    } else if (window.localStorage.checkLogin === 'true') {
      next();
    } else {
      router.replace({
        name: 'Home',
      });
    }
});


export default router;



