<template>
  <div class="register--container">
    <div class="container--title">
      <img class="title--img" src="https://om536p71r.qnssl.com/%E9%93%B6%E8%A1%8C%20%281%29.png">
      <span class="title--text">共信金融</span>
    </div>
    <div class="input-group--container">
      <div class="input--container">
        <input class="input--el" placeholder="11位手机号" type="text" v-model="phoneInput">
        <span class="input--icon"><i class="iconfont">&#xe6d7;</i></span>
      </div>
      <div class="input--container">
        <input class="input--el" placeholder="手机验证码" type="text" v-model="verifyCode">
        <span class="input--icon"><i class="iconfont">&#xe664;</i></span>
        <div class="right-btn--area">
          <div @click="sendVerifyCode" class="global--button_primary radius">获取验证码</div>      
        </div>
      </div>
      <div class="input--container">
        <input class="input--el" placeholder="新的密码" type="password" v-model="passwordInput">
        <span class="input--icon"><i class="iconfont">&#xe65e;</i></span>
      </div>
      <div class="input--container">
        <input class="input--el" placeholder="确认密码" type="password" v-model="confirmPassword">
        <span class="input--icon"><i class="iconfont">&#xe65e;</i></span>
      </div>
      <div class="center-btn--area">
        <div @click="changePassword" class="global--button_primary radius">确认</div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        phoneInput:'',
        verifyCode:'',
        passwordInput:'',
        confirmPassword:'',
      };
    },
    mounted() {
    },
    methods: {
      changePassword(){
        this.axios.get(`user/changePassword?phone=${this.phoneInput}&
        password=${this.passwordInput}&code=${this.verifyCode}`)
          .then((res) => {
            console.log(res);
            if(res.code === 1){
              window.localStorage.checkLogin = true;
                this.$router.replace({
                  name: 'Borrow',
                });
            } else if (res.code === 306){
              alert('验证码错误');
            } else if (res.code === 307){
              alert('验证码已失效，请重新发送验证码！');
            }
          });
      },
      sendVerifyCode(){
        console.log(`${this.phoneInput}`);
        this.axios.get(`code/send?phone=${this.phoneInput}`)
          .then((res) => {
            if(res.code === 1){
              this.sended = false;
              this.sending = true;
            } else if(res.code === 304){
              alert('60秒内不可重复发送验证码');
            } else if(res.code === 301){
              alert('账户不存在，请先注册账户');
            }
          });
      },
    },
  };
</script>



