<template>
  <div class="identify--container">
    <div class="header--content">
      <img src="https://om536p71r.qnssl.com/%E5%A4%B4%E5%83%8F.png" alt="" class="avatar">
    </div>
    <div class="input-table-cell--group">
      <div class="group--content">
        <div class="content--left-part">姓名</div>
        <input v-model="userInfo.name" placeholder="输入姓名" class="content--right-part"></input>
      </div>
      <div class="group--content">
        <div class="content--left-part">手机号码</div>
        <input v-model="userInfo.phone" placeholder="输入手机号" class="content--right-part"></input>
      </div>
      <div class="group--content">
        <div class="content--left-part">身份证</div>
        <input v-model="userInfo.idNum" placeholder="输入身份证" class="content--right-part"></input>
      </div>
      <div class="group--content">
        <div class="content--left-part">微信号</div>
        <input v-model="userInfo.wechatId" placeholder="输入微信号" class="content--right-part"></input>
      </div>
      <div class="group--content">
        <div class="content--left-part">QQ号</div>
        <input v-model="userInfo.qqId" placeholder="输入QQ号" class="content--right-part"></input>
      </div>
    </div>
    <textarea v-model="userInfo.address" class="address--input" id="" cols="10" rows="4" placeholder="请输入您现在的住址"></textarea>
    <div class="center-btn--area">
      <div class="global--button_primary radius" @click="submitHandler()">提交</div>
    </div>
    <div class="takeplace"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userInfo: {
        name:'',
        idNum:'',
        phone:'',
        wechatId:'',
        qqId:'',
        address:'',
      },
    };
  },
  mounted() {
    this.axios.get('user/identity')
        .then((res) => {
          if(res.code === 1){
            this.userInfo = res.data;
          }
        });
  },
  methods: {
    submitHandler() {
      console.log(this.userInfo);
      this.axios.get(`user/identify?name=${this.userInfo.name}&phone=${this.userInfo.phone}&id_num=${this.userInfo.idNum}
      &wechat_id=${this.userInfo.wechatId}&qq_id=${this.userInfo.qqId}&address=${this.userInfo.address}`)
          .then((res) => {
            if(res.code === 1){
                this.$router.replace({
                  name: 'Borrow',
            });
            } else {
              alert('认证信息保存失败，请重试！');
              return ;
            }
          });
    },
  }
};
</script>



