<template>
  <div class="borrow--container">
    <div class="header--content">
        <span class="text">我要借款</span>
    </div>
    <div class="table--container">
      <section class="table-cell--content">
        <img class="cell--icon" src="https://om536p71r.qnssl.com/%E6%94%AF%E4%BB%98%E6%96%B9%E5%BC%8F.png" alt="">
        <input class="right-part" type="number" v-model="borrow" min="1" max="100000" placeholder="请输入需借款数字(元)"></input>
      </section>
      <section class="table-cell--content">
        <img class="cell--icon" src="https://om536p71r.qnssl.com/%E6%94%AF%E4%BB%98%E6%96%B9%E5%BC%8F%20%281%29.png" alt="">
        <select class="selection" v-model="type">
          <option value ="现金">现金</option>
          <option value ="银行卡">银行卡</option>
          <option value="支付宝">支付宝</option>
          <option value="微信支付">微信支付</option>
        </select>
      </section>
      <section class="table-cell--content">
        <img class="cell--icon" src="https://om536p71r.qnssl.com/%E8%AE%A2%E5%8D%95%E8%AF%A6%E6%83%85-%E6%88%AA%E6%AD%A2%E6%97%A5%E6%9C%9F.png" alt="">
        <select class="selection" v-model="deadline_at">
          <option value ="7">7天后还款</option>
          <option value ="10">10天后还款</option>
          <option value ="15">15天后还款</option>
          <option value="30">1个月后还款</option>
          <option value="60">2个月后还款</option>
          <option value="90">3个月后还款</option>
          <option value="120">4个月后还款</option>
          <option value="150">5个月后还款</option>
          <option value="180">6个月后还款</option>
        </select>
      </section>
    </div>
    <div class="center-btn--area">
      <div @click="submitHandler" class="global--button_primary radius">确认借款</div>
    </div>
    <div class="popup-dialog--container"  :class="dialogStatus ? 'active' : ''">
      <div class="mask"></div>
      <div class="dialog--main-container">
        <div class="dialog--header"></div>
        <div class="dialog--body">
          <i class="iconfont body--icon">&#xe675;</i>
          <div class="body--desc">您的借款申请已提交至客服</div>
        </div>
        <div class="dialog--footer">
          <div @click="confirmHandler" class="global--button_primary">确定</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        dialogStatus: false,
        borrow: '',
        type: '支付宝',
        deadline_at: '7',
      };
    },
    mounted() {
    },
    methods: {
      confirmHandler() {
        this.dialogStatus = false;
      },
      submitHandler(){
        this.axios.get(`bill/borrow?borrow=${this.borrow}&type=${this.type}&deadline_at=${this.deadline_at}`)
            .then((res) => {
                console.log(res);
              if(res.code === 1){
                this.dialogStatus = true;
              } else {
                alert('借款申请提交失败，请重新提交');
              }
            });
      }
    },
  };
</script>



