<template>
  <div class="borrow--list--container">
    <div class="header--content">
      <img src="http://via.placeholder.com/150x150" alt="" class="avatar">
    </div>
    <div class="table--container" >
        <table class="table">
            <tr class="head">
                <th>申请时间</th>
                <th>用户姓名</th>
                <th>手机号码</th>
                <th>放款方式</th>
            </tr>
            <tr class="element" v-for="item in borrowList">
                <td>{{item.applied_at}}</td>
                <td>{{item.name}}</td>
                <td>{{item.phone}}</td>
                <td>{{item.type}}</td>
            </tr>
        </table>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableHead:[],
        tableElement:[],
        borrowList:[],
      };
    },
    mounted() {
      this.axios.get(`bill/borrowList?status=${this.$route.params.status}`)
          .then((res) => {
              console.log(res);
              if(res.code === 1){
                  this.borrowList = res.data;
              } else {
                alert('请尝试刷新或重新登录。');
              }
          });
    },
    methods: {

    },
  };
</script>



