<template>
  <div class="my--borrow--container">
    <div class="header--content">
      <img src="https://om536p71r.qnssl.com/%E5%A4%B4%E5%83%8F.png" alt="" class="avatar">
    </div>

    <!-- 结构和类名按这个写，都规划好了 -->
    <div class="table--container">
      <section class="table-cell--content" v-for="item in selectOptions">
        <div class="router" @click="redirectToBorrowList(item.status)">
          <img class="cell--icon" :src="item.img_url" alt="">
          <div class="right-part">{{item.title}}</div>
        </div>
      </section>
    </div>
    <div class="center-btn--area">
      <div class="global--button_primary radius" @click="logout()">退出登录</div>
    </div>
    <div class="takeplace"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectOptions:[
        {
          'title':'未下款',
          'to':'BorrowPaid',
          'status':'0',
          'img_url':'https://om536p71r.qnssl.com/%E6%9C%AA%E5%88%B0%E8%B4%A6.png',
        },
        {
          'title':'已下款',
          'to':'BorrowNotPaid',
          'status':'1',
          'img_url':'https://om536p71r.qnssl.com/%E5%B7%B2%E5%88%B0%E8%B4%A6.png',
        }
      ]
    };
  },
  methods: {
    logout(){
      this.axios.get('user/logout')
          .then((res) => {
            console.log(res);
            window.localStorage.checkLogin = false;
            this.$router.replace({
              name: 'Home',
            });
          });
    },
    redirectToBorrowList(status){
      console.log(this.$route);
      this.$router.push({
              name: 'BorrowList',
              params:{
                status: status,
              },
            });
    },
  },
};
</script>



