<template>
  <div class="register--container">
    <div class="container--title">
      <img class="title--img" src="https://om536p71r.qnssl.com/%E9%93%B6%E8%A1%8C%20%281%29.png">
      <span class="title--text">共信金融</span>
    </div>
    <div class="input-group--container">
      <div class="input--container">
        <input class="input--el" placeholder="11位手机号" type="text" v-model="phoneInput">
        <span class="input--icon"><i class="iconfont">&#xe6d7;</i></span>
      </div>
      <div class="input--container">
        <input class="input--el" placeholder="手机验证码" type="text" v-model="verifyCode">
        <span class="input--icon"><i class="iconfont">&#xe664;</i></span>
        <div class="right-btn--area">
          <div v-if="!sended" @click="sendVerifyCode" class="global--button_primary radius">获取验证码</div>      
          <div  v-if="sended" class="global--button_primary radius" style="background-color:grey">60秒后</div>      
        </div>
      </div>
      <div class="input--container">
        <input class="input--el" placeholder="登录密码" type="password" v-model="passwordInput">
        <span class="input--icon"><i class="iconfont">&#xe65e;</i></span>
      </div>
      <div class="input--container">
        <input class="input--el" placeholder="确认密码" type="password">
        <span class="input--icon"><i class="iconfont">&#xe65e;</i></span>
      </div>
      <div class="input--container">
        <div class="select--box">
          <span class="select--type">选择您的身份：</span>
          <select class="input--el"  v-model="ifAdult">
            <option value ="1">成人</option>
            <option value ="0">学信</option>
          </select>
        </div>
        <span class="input--icon"><i class="iconfont">&#xe621;</i></span>
      </div>
      <div class="input--container">
        <input class="input--el" placeholder="推荐码（选填）" type="text" v-model="referrerInput">
        <span class="input--icon"><i class="iconfont">&#xe61f;</i></span>
      </div>
      <!-- <div class="input--container">
        <input class="input--el" placeholder="图形验证码" type="text" name="" value="">
        <span class="input--icon"><i class="iconfont">&#xe678;</i></span>
      </div> -->
      <div class="checkbox--container">
        <input class="checkbox" type="checkbox" v-model="checkboxInput" @click="checkBox">
        <span>我已阅读并同意</span><router-link to="TemsAndConditions">《服务与隐私协议》</router-link>
      </div>
      <div class="center-btn--area">
        <div @click="register" class="global--button_primary radius">下一步</div>
      </div>
    </div>
    <div class="secret--area">
        <span class="secret--text">禁止在校学生申请！</span>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        sended: false,
        phoneInput: '',
        verifyCode: '',
        passwordInput: '',
        referrerInput: '',
        checkboxInput: false,
        ifAdult: '1',
        counter: 60,
      };
    },
    mounted() {
    },
    methods: {
      register(){
        if(!this.checkboxInput){
          alert('请确认服务与隐私协议');
          return;
        }
        this.axios.get(`user/register?phone=${this.phoneInput}&
        password=${this.passwordInput}&code=${this.verifyCode}&referrer=${this.referrerInput}&adult=${this.ifAdult}`)
          .then((res) => {
            console.log(res);
            if(res.code === 1){
              window.localStorage.checkLogin = true;
                this.$router.replace({
                  name: 'Identify',
                });
            } else if (res.code === 305){
              alert('该手机已注册，请直接登录！');
            } else if (res.code === 306){
              alert('验证码错误');
            } else if (res.code === 307){
              alert('验证码已失效，请重新发送验证码！');
            }
          });
      },
      sendVerifyCode(){
        console.log(`${this.phoneInput}`);
        this.axios.get(`code/send?phone=${this.phoneInput}`)
          .then((res) => {
            if(res.code === 1){
              this.sended = true;
              this.counterFor60s();
            } else if(res.code === 304){
              alert('60秒内不可重复发送验证码');
            } else if(res.code === 305){
              alert('该手机已注册，请直接登录！');
            }
          });
      },
      checkBox(){
        console.log(`${this.checkboxInput}`);
      },
      counterFor60s(){
        counterHandler = setInterval(()=>{
          if(this.counter>0){
            this.counter--;
          } else {
            this.sended = false;
            this.counter = 60;
            clearInterval(counterHandler);
          }
        }, 1000)
      },
    },
  };
</script>



