<template>
  <div class="message-list--container">
    <div class="list" v-for="item in messageLists">
      <div class="cell">
        <img class="cell--icon" src="https://ws2.sinaimg.cn/large/006tKfTcgy1flsfipv6luj305k05kq2r.jpg">
        <div class="right-part--content">
          <div class="right--top--part">
            <div class="left--part unread" v-if="item.read_status == 0">
              通知消息[未读]
              <span class="red--bubble"></span>
            </div>
            <div class="left--part read" v-if="item.read_status == 1">
            通知消息
            </div>
            <div class="time">
              {{item.time}}
            </div>
          </div>
          <div class="right--bottom--part">
            <div class="text">
              {{item.shortDescription}}
            </div>
          </div>  
        </div>
      </div> 
    </div>
    <div class="takeplace"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      messageLists: [
        {
          message_id: "1",
          shortDescription: "恭喜您成功申请借款5000元。",
          time: "今天12：00",
          read_status: "0"
        },
        {
          message_id: "2",
          shortDescription: "恭喜您成功申请借款10000元。",
          time: "今天18：00",
          read_status: "1"
        }
      ]
    };
  },
  mounted() {},
  methods: {}
};
</script>



